import React, { useState, useEffect } from "react";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import MenuItem from "@mui/material/MenuItem";
import 'react-dates/lib/css/_datepicker.css';
import FormControl from "@mui/material/FormControl";
import OutlinedInput from "@mui/material/OutlinedInput";
import Select from "@mui/material/Select";
import FormLabel from "@mui/material/FormLabel";
import Button from "@mui/material/Button";
import VisibilityIcon from "@mui/icons-material/Visibility";
import SaveIcon from "@mui/icons-material/Save";
import DeleteIcon from "@mui/icons-material/Delete";
import Stack from "@mui/material/Stack";
import { TextField } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { holidayDetails, setErrorMessage, setSuccessMessage } from "../Slice/holidaySlice";
import { useHistory, useParams } from 'react-router-dom';
import { insertHoliday, deleteHoliday, readHoliday, setHolidayData, updateHoliday, holidaySuccessMessage, holidayErrorMessage } from "../Slice/holidaySlice";
import EditIcon from '@mui/icons-material/Edit';

export default function AddHoliday() {
  const dispatch = useDispatch();
  const data = useSelector(holidayDetails);
  const successMessage = useSelector(holidaySuccessMessage)
  const errorMessage = useSelector(holidayErrorMessage)
  let { id, actionType } = useParams();
  const [isUpdateMode, setIsUpdateMode] = useState(false);
  const history = useHistory();
  const locationOptions = ["All Location", "Bettendorf,IA", "India"]
  const typeOptions = ["Optional", "Fixed"]
  const initialValues = {
    year: new Date().getFullYear(),
    holidays: [],
    holidayData: {
      id: "",
      location: "",
      date: "",
      type: "",
      description: "",
      errorMessage: "",
      successMessage: "",
    }
  }

  const [values, setValues] = useState(initialValues.holidayData);
  const handleDelete = () => {
    dispatch(deleteHoliday(values.id));
    history.push(`/`)
  }
  useEffect(() => {
    dispatch(setSuccessMessage(""))
    dispatch(setErrorMessage(""))
    if (actionType === "create") {
      dispatch(setHolidayData({}));

    } else if (actionType === "view" || actionType === "update") {
      dispatch(readHoliday(id));
    }
  }, [dispatch, actionType, id])

  useEffect(() => {
    if (actionType === "create" && isUpdateMode) {
      if (data.status === "201") {
        history.push(`/update/${data.id}`);
        setValues(data);
      }
    } else if (actionType === "view" || actionType === "update" && !isUpdateMode) {
      setValues(data);
    }
  }, [data, actionType, history, isUpdateMode])
  //save button

  const handleSubmit = (e) => {
    e.preventDefault();

    if (actionType === "update") {
      dispatch(updateHoliday(id, values))
    } else if (actionType === "create") {
      dispatch(insertHoliday(values))
      setIsUpdateMode(true)
    }

  }
  return (
    <Box
      component="form"
      onSubmit={handleSubmit}
      sx={{
        width: "1100px",
        height: "10",
        marginTop: "20px",
        marginLeft: "150px",
        border: "1px solid blue",
        p: 2,
        display: "flex",
      }}
    >
      <FormControl  >

        <Grid container >
          <FormLabel
            sx={{ ml: 8 }}
          >
            Holiday ID:
            <OutlinedInput
              outline="none"
              placeholder="#"
              disabled={true}
              value={data.id}

              sx={{
                ml: 3,
                mb: 1,
                mr: 1,
                width: "100ch",
                height: 40,
                outline: "none",
              }}
            ></OutlinedInput>
          </FormLabel>

          <FormLabel sx={{ ml: 9 }}>
            Location:
            <FormControl
              fullWidth
              placeholder="#"
              size="small"
              sx={{
                ml: 3.5,
                mr: 0,
                mb: 1,
                width: "100ch",


              }}
            >
              <Select
                placeholder="#"
                type="text"
                id="location"

                disabled={actionType === "view"}

                value={values.location}
                onChange={(e) => {
                  setValues({ ...values, location: e.target.value })
                }}
              >
                <MenuItem value={false} > </MenuItem>
                {locationOptions.map((locationForSelect, index) => (
                  <MenuItem value={locationForSelect} key={index}>{locationForSelect}</MenuItem>
                ))}
              </Select>
            </FormControl>
          </FormLabel>
          <FormLabel sx={{ ml: 12, mr: 0 }}>
            Date:
            <TextField
              height="30"
              format="yyyy-MM-dd"
              className="form-control"
              id="date"
              type="date"
              onChange={(e) => {
                setValues({ ...values, date: e.target.value })
              }}
              disabled={actionType === "view"}
              value={values.date}
              size="small"
              sx={{ ml: 4, mr: 0, mb: 1, width: "100ch", fontHeight: 20 }}
            />
          </FormLabel>
          <FormLabel sx={{ ml: 5 }}>
            Holiday Type:
            <FormControl
              fullWidth
              size="small"
              sx={{
                ml: 3.5,
                width: "100ch",
              }}
            >
              <Select
                type="text"
                className="form-control"
                id="Holiday Type"

                value={values.type}
                disabled={actionType === "view"}
                onChange={(e) => {
                  setValues({ ...values, type: e.target.value })
                }}
              >
                <MenuItem value={false} > </MenuItem>
                {typeOptions.map((typeForSelect, index) => (
                  <MenuItem value={typeForSelect} key={index}>{typeForSelect}</MenuItem>
                ))}
              </Select>
            </FormControl>
          </FormLabel>
          <FormControl fullWidth size="small" >
            <FormLabel sx={{ ml: 6, mr: 0 }} size="small">
              Description:
              <TextField
                placeholder=" Select Description"
                height="30"
                margin="dense"
                size="small"
                value={values.description}
                type="text"
                onChange={(e) => {
                  setValues({ ...values, description: e.target.value })
                }}
                disabled={actionType === "view"}
                sx={{ ml: 4, mr: 0, mb: 3, mt: 1, width: "100ch", minHeight: 30 }}
              ></TextField>
            </FormLabel>
          </FormControl>
        </Grid>
        <Box color="green" textAlign="center" fontSize={16} fontFamily="'Montserrat', sans-serif" >{successMessage} </Box>
        <Box color="red" mt={2} mb={2} textAlign="center" fontSize={16} fontFamily="'Montserrat', sans-serif">{errorMessage}</Box>

        <Stack
          direction="row"
          spacing={2}
          style={{ justifyContent: "center", textTransform: "none", }}
        >
          <div>
            {(actionType === "create" || actionType === "update") && (
              <Button
                className="save"
                spacing={2}
                style={{ textTransform: "none" }}
                startIcon={<SaveIcon style={{ color: "yellow" }} />}
                variant="contained"
                color="primary"
                type="submit"
              >
                Save
              </Button>
            )}
            &nbsp;
            {((actionType === "create" && data.id != undefined) || actionType === "update") && (
              <Button
                className="delete"
                href=""
                style={{ textTransform: "none" }}
                type="submit"
                startIcon={<DeleteIcon style={{ color: "yellow" }} />}
                variant="contained"
                color="error"

                onClick={() => handleDelete(data.id)}
                spacing={1}
              >
                Delete
              </Button>
            )}
            &nbsp;&nbsp;
            {(actionType === "view") && (
              <Button
                onClick={() => { history.push(`/update/${id}`) }}
                style={{ color: "#04a2ad", textTransform: "none" }}
                startIcon={<EditIcon style={{ color: "green" }} />}
                variant="outlined"
              >
                Switch To Update
              </Button>
            )}
            {((actionType === "create" && data.id != undefined) || (actionType === "update")) && (
              <Button
                onClick={() => { history.push(`/view/${id}`) }}
                style={{ color: "#04a2ad", textTransform: "none" }}
                startIcon={<VisibilityIcon style={{ color: "blue" }} />}
                variant="outlined"
              >
                Switch to view
              </Button>
            )}
            &nbsp;
            <Button
              onClick={() => {
                history.push(`/`)
              }}
              style={{ color: "#04a2ad", textTransform: "none" }}
              variant="outlined"
            >
              Goto:Holidays
            </Button>
          </div>
        </Stack>
      </FormControl>
    </Box>
  );
}







