import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import { useEffect } from "react";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import { Link } from "react-router-dom"
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import MenuItem from '@mui/material/MenuItem';
import { useDispatch } from "react-redux";
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Box from '@mui/material/Box';
import VisibilityIcon from "@mui/icons-material/Visibility";
import EditIcon from '@mui/icons-material/Edit';
import {  setYear, fetchHolidays, yearFromState, holidaysList,holidayErrorMessage  } from "../Slice/holidaySlice";
import { useSelector } from 'react-redux';
import IconButton from '@mui/material/IconButton';

export default function HolidayList() {
const dispatch = useDispatch();
  const currentYear = new Date().getFullYear();
  const year = useSelector(yearFromState);
  const holidays = useSelector(holidaysList);
  const errorMessage = useSelector(holidayErrorMessage)
  const handleSearch = (event) => {
    const selectedYear = event.target.value;
    if (selectedYear !== 0) {
      dispatch(setYear(selectedYear));
    } else {
      dispatch(holidayErrorMessage (""));
    }
  }
  let yearArray = [];
  for (let i = -3; i <= 3; i++) {
    yearArray[i + 3] = currentYear + i;
  }
useEffect(() => {
    dispatch(fetchHolidays(year));
  }, [year])
  return (
    <AppBar position="static" style={{ height: '40px', color: 'white', display: 'flex', background: '#04a2ad', width: 1450 }}>
      <Grid container direction="row" alignItems="center">
        <Typography component="div" sx={{ mr: 5, ml: 18, display: { md: 'flex' } }}
        >
          Filter-year:
        </Typography>
        <Box sx={{
          width: 140,
          maxWidth: '50%',
          height: '40px',
          background: "white",
          outline: 'none',
          mb: 0
        }}>
          <FormControl fullWidth size="small">
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={year}
              onChange={handleSearch}
            >
              {yearArray.map((yearForSelect, index) => (
                <MenuItem key={index} dense value={yearForSelect}>{yearForSelect}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Box>
      </Grid>
      <Grid container maxWidth="xl" elevation={0} style={{ background: 'black', width: '100', height: '40px', fontFamily: "'Montserrat', sans-serif", }}>
        <Grid
          component="div" item ml={2} mr={2} mt={1}
          sx={{ display: { color: 'white', textSize: 8 } }}
        >
          Action
        </Grid>
        <Grid>
          
          <Link to={{
            pathname: "/create/0"
          }}>
            <IconButton>
              <AddCircleIcon sx={{ mr: 4, color: 'yellow', pb: 2, mb: 2, "& td": { border: 0 } }} />
            </IconButton>
          </Link>
        </Grid>
        <Grid item xs={1} mt={1} sx={{ color: 'white', borderBottom: "none", display: 'fix', fontStyle: 'default' }} align="left">Date</Grid>
        <Grid item xs={3} mt={1} sx={{ color: 'white', borderBottom: "none", display: 'fix', fontStyle: 'default' }} align="left">Description</Grid>
        <Grid item xs={3} mt={1} sx={{ color: 'white', borderBottom: "none", display: 'fix', fontStyle: 'default' }} align="left">Type</Grid>
        <Grid item mt={1} sx={{ color: 'white', borderBottom: "none", display: 'fix', fontStyle: 'default' }} align="left">Location</Grid>
      </Grid>
      {holidays && holidays.holidays && holidays.holidays.length > 0 ? (
        <Grid container spacing={1} direction="row" justify="center" alignItems="center" sx={{ ml: 2, mt: 1, display: { md: 'flex', color: 'black', fontFamily: "'Montserrat', sans-serif", direction: "" } }} elevation={0} >
          {holidays.holidays.map((holiday, index) => (
            <Grid container sx={{ display: { color: 'black' } }} elevation={0} key={index} value={holiday} >

              <Link to={'/view/' + holiday.id}  >
                <IconButton>
                  <VisibilityIcon fontSize="small" sx={{ mt: 0, mr: 2, color: '#04a2ad', "& td": { border: 0 } }} />
                </IconButton>
              </Link>
              <Link to={'/update/' + holiday.id} >
                <IconButton>
                  <EditIcon sx={{ color: 'rgb(221, 94, 20)' }} />
                </IconButton>
              </Link>
              <Grid item xs={1} mr={1} ml={4} mt={1} align="left" >{holiday.date}</Grid>
              <Grid item xs={3} mt={1} align="left" >{holiday.description}</Grid>
              <Grid item xs={3} mt={1} align="left" >{holiday.type}</Grid>
              <Grid item mr={1} mt={1} align="left" >{holiday.location}</Grid>
            </Grid>
          ))}
        </Grid>
      ) : (
        <Grid item xs={3} align='center' sx={{ color: 'red', display: 'center' }} >
           
          <Box m="auto" mt={4}>
           <Typography color="red" mt={2} mb={2} textAlign="center" fontSize={16} fontFamily= "'Montserrat', sans-serif">{errorMessage} </Typography>
        </Box>
         </Grid>
       )}
    </AppBar>
  )
} 