import axios from "axios";
import { HOLIDAYS,CHECKCONN } from '../endpoints';
import { CREATEHOLIDAY_API } from '../endpoints';
import { createSlice } from '@reduxjs/toolkit'

const currentYear = new Date().getFullYear();
const holidaySlice = createSlice({
    name: 'holidaySlice',
    initialState: {
        year: currentYear,
        holidays: [],
        successMessage: "",
        errorMessage: "",
        holidayData: {
            id: "",
            location: "",
            date: "",
            type: "",
            description: "",
        },
    },
    reducers: {
        setYear(state, action) {
            state.year = action.payload;
        },
        setHolidays(state, action) {
            state.holidays = action.payload;
            state.error = "";
        },
        setHolidayData(state, action) {
            state.holidayData = action.payload;
            state.error = "";
        },
        setSuccessMessage(state, action) {
            state.successMessage = action.payload;
        },
        setErrorMessage(state, action) {
            state.errorMessage = action.payload;
        }
    },
});
export const { setYear, setHolidayData, setHolidays, setSuccessMessage, setErrorMessage } = holidaySlice.actions;

// thunk functions 

export const fetchHolidays = (year) => async (dispatch) => {
    await axios.get(`${HOLIDAYS}/${year}`)
        .then((response) => {
            dispatch(setHolidays(response.data));
            dispatch(setYear(year));
            dispatch(setErrorMessage(""));
        })
        .catch((error) => {
            if (error.response.code = "ERR_NETWORK" && !error.response) {
                dispatch(setErrorMessage("Error while connecting to database"));
            } else if (error.response.status === 404) {
                dispatch(setErrorMessage("Holiday not found by year " + year))
            } else {
                dispatch(setErrorMessage("Error while connecting to database"))
            }
            dispatch(setHolidays([]));
        })
};

export const insertHoliday = (holidayData) => async (dispatch) => {
    if (holidayData.id === "") {
        await axios.post(`${CREATEHOLIDAY_API}`, holidayData)
            .then((response) => {
                if (response.status === 201) {
                    dispatch(setSuccessMessage("Holiday created successfully"));
                    dispatch(setHolidayData(response.data.holidayData));
                    dispatch(setErrorMessage(""));
                } else if (response.status === 500) {
                    dispatch(setErrorMessage(" Unable to connect DB  "));
                }
                else if (response.status == 200) {
                    dispatch(setErrorMessage("Holiday already exist"));
                    dispatch(setSuccessMessage(""));
                } else if (response.code == 'ER_TRUNCATED_WRONG_VALUE') {
                    dispatch(setErrorMessage("Unable to connect DB"));
                    dispatch(setSuccessMessage(""));
                }
                else {
                    dispatch(setSuccessMessage(response.data.errorMessage));
                    dispatch(setHolidayData(response.data.holidayData, ...holidayData));
                    dispatch(setErrorMessage(""));
                }
            })
            .catch((err) => { 
                if (err.response.code = "ERR_NETWORK" && !err.response) {
                    dispatch(setErrorMessage("Error while connecting to database"));
                } else if (err.response.status === 400) {
                    dispatch(setErrorMessage("Please provide all required fields "))
                } else {
                    dispatch(setErrorMessage("Connection not available"))
                }
                dispatch(setSuccessMessage(""));
            })
    } else {
        //update Query
        await axios.put(`${CREATEHOLIDAY_API}/${holidayData.id}`, holidayData)
            .then((response) => {
                dispatch(setHolidayData(response.holiday));
                dispatch(setSuccessMessage("Holiday updated successfully"))
                dispatch(setErrorMessage(""))
            })
            .catch((error) => {
                dispatch(setHolidayData(holidayData));
                dispatch(setErrorMessage("Some Problem happened during update"))
                dispatch(setSuccessMessage(""))
            })
    }
};
export const readHoliday = (id) => async (dispatch) => {
    await axios.get(`${CREATEHOLIDAY_API}/${id}`)
        .then((response) => {
            dispatch(setHolidayData(response.data));
        })
        .catch((error) => {
            dispatch(setErrorMessage("Unable to connect DB ."))
            dispatch(setSuccessMessage(""))
        })
};

export const updateHoliday = (id, holidayData) => async (dispatch) => {
    let isConnectionAvailable = true;
    await axios.get(`${CHECKCONN}`)
        .then((response) => {
            isConnectionAvailable = true
        })
        .catch((error) => {
            isConnectionAvailable = false;
            if(error.code ==="ERR_NETWORK"){
                dispatch(setErrorMessage("Web service unavailable"))
                dispatch(setSuccessMessage(""));
            }else if(error.code === "ERR_BAD_RESPONSE"){
                dispatch(setErrorMessage("DB Connection not available"))
                dispatch(setSuccessMessage(""));
            }
            else{
                dispatch(setErrorMessage("Unable to connect DB ."))
                dispatch(setSuccessMessage(""));
            }
        })
    if(isConnectionAvailable){
        await axios.put(`${CREATEHOLIDAY_API}/${id}`, holidayData)
           .then((response) => {
               if (response.status === 200) {
                   dispatch(setHolidayData(response.data));
                   dispatch(setHolidayData(holidayData));
                   dispatch(setSuccessMessage("Holiday Updated Succesfully"))
                   dispatch(setErrorMessage(""))
               } else if (response.code === "ERR_CONNECTION_REFUSED") {
                   dispatch(setErrorMessage(" Unable to connect DB  "));
               }
               
               else {
                   dispatch(setSuccessMessage(response.data.errorMessage));
                   dispatch(setHolidayData(response.data.holidayData, ...holidayData));
                   dispatch(setErrorMessage(""));
               }
           })
           .catch((error) => {
               if (error.code === "ERR_BAD_REQUEST") {
                   dispatch(setErrorMessage("Please provide all required fields"))
   
               } else if (error.status === 406) {
                   dispatch(setErrorMessage("Holiday already exist"));
                   dispatch(setSuccessMessage(""));
   
               } else if (error.code === "ERR_NETWORK "){
                   dispatch(setErrorMessage(" DB Connection not available  "));
                   
               }
               dispatch(setSuccessMessage(""))
           })
    }    
};

export const deleteHoliday = (id) => async (dispatch) => {
    await axios.delete(`${CREATEHOLIDAY_API}/${id}`)
        .then((response) => {
            dispatch(setErrorMessage("Holiday deleted Succesfully"))
            dispatch(setSuccessMessage(""))
        })
        .catch((error) => {
            dispatch(setErrorMessage("DB Error while deleting holiday"))
            dispatch(setSuccessMessage(""))
        })
};
export const yearFromState = (state) => state.year;
export const holidayDetails = (state) => state.holidayData;
export const holidaysList = (state) => state.holidays;
export const holidaySuccessMessage = (state) => state.successMessage;
export const holidayErrorMessage = (state) => state.errorMessage;
export default holidaySlice.reducer

